.Header {
  height: 95vh;
  width: 100%;
  background-image: -webkit-gradient(linear, left top, right bottom, from(rgba(255,223,50,0.8)), to(rgba(255,215,0,0.8)))
  , url('/imgs/Logo.png');
  background-image: -webkit-linear-gradient(top left, rgba(255,223,50,0.8), rgba(255,215,0,0.8))
  , url('/imgs/Logo.png');
  background-image: -o-linear-gradient(top left, rgba(255,223,50,0.8), rgba(255,215,0,0.8))
  , url('/imgs/Logo.png');
  background-image: linear-gradient(to bottom right, rgba(255,223,50,0.8), rgba(255,215,0,0.8))
  , url('/imgs/Logo.png');
  background-position:center center;
  background-repeat: no-repeat;
  background-size: cover;
}

.Headerlogo {
  margin-bottom: 5rem;
  height: 64%;
  text-align: center;
}

.Header img {
  margin-top: 3rem;
  height: 100%;
  -webkit-animation: Appear 1.5s ease-in;
          animation: Appear 1.5s ease-in;
}

.Header h1 {
  text-align: center;
  text-transform: uppercase;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  font-size: 2.5rem;
  font-weight: 900;
  display: block;
  color: darkblue;
  -webkit-animation: moveInBottom 1s ease-out;
          animation: moveInBottom 1s ease-out;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
}

@-webkit-keyframes moveInBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10rem);
            transform: translateY(10rem);
  }

  80% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
    opacity: .8;
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@keyframes moveInBottom {
  0% {
    opacity: 0;
    -webkit-transform: translateY(10rem);
            transform: translateY(10rem);
  }

  80% {
    -webkit-transform: translateY(-1rem);
            transform: translateY(-1rem);
    opacity: .8;
  }

  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
  }
}

@-webkit-keyframes Appear {
  0% {
    opacity: 0;
  }

  80% {
    opacity: .8;
  }

  100% {
    opacity: 1;
  }
}

@keyframes Appear {
  0% {
    opacity: 0;
  }

  80% {
    opacity: .8;
  }

  100% {
    opacity: 1;
  }
}

.Body {
  background-color: black;
}

.Body h2 {
  color: white;
  text-transform: uppercase;
  display: block;
  font-size: 2.5rem;
  font-weight: 700;
  padding: 4rem 0 1rem 2rem;
}

@media (max-width: 499px) {
  .DesktopHeader {
    display: none;
  }
}

@media (min-width: 500px) {
  .DesktopHeader {
    height: 80vh;
    width: 100%;
    background-image: -webkit-gradient(linear, left top, right bottom, from(rgba(255,223,50,0.7)), to(rgba(255,215,0,0.7)))
    , url('/imgs/DesktopHeader.jpg');
    background-image: -webkit-linear-gradient(top left, rgba(255,223,50,0.7), rgba(255,215,0,0.7))
    , url('/imgs/DesktopHeader.jpg');
    background-image: -o-linear-gradient(top left, rgba(255,223,50,0.7), rgba(255,215,0,0.7))
    , url('/imgs/DesktopHeader.jpg');
    background-image: linear-gradient(to bottom right, rgba(255,223,50,0.7), rgba(255,215,0,0.7))
    , url('/imgs/DesktopHeader.jpg');
    background-position:right center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .DesktopHeader h1 {
    text-align: center;
    text-transform: uppercase;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    font-size: 4rem;
    font-weight: 900;
    display: block;
    color: darkblue;
    -webkit-animation: moveInBottom 1s ease-out;
            animation: moveInBottom 1s ease-out;
    -webkit-animation-delay: 1.5s;
            animation-delay: 1.5s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
  }
  .DesktopHeader img {
    margin-top: 2rem;
    height: 100%;
    -webkit-animation: Appear 1.5s ease-in;
            animation: Appear 1.5s ease-in;
  }
  .DesktopHeaderlogo {
    height: 80%;
    margin-bottom: 4rem;
    text-align: center;
  }
  .Header {
    display: none;
  }
  .Body h2 {
    margin-left: 6rem;
  }
}


